import React, { Component } from "react";
import styled from "styled-components";

export const Div = styled.div`
  position: ${props => (props.showSidebar ? "fixed" : "absolute")};
  top: 0;
  left: ${props => (props.showSidebar ? "0" : "-140px")};
  width: 140px;
  height: 100px;
  border: 1px solid #e9e8e7;
  transition: all 500ms;
`;

class Sidebar extends Component {
  componentDidMount = () => {
    const element = this.refs.element;
  };
  render() {
    const { showSidebar, articleData } = this.props;
    return (
      <Div showSidebar={showSidebar} ref="element">
        {console.log(showSidebar, articleData)}
        <h2>{articleData.subtitle}</h2>
      </Div>
    );
  }
}

export default Sidebar;
